<template>
  <div>
    <div class="container1 fl-ex fl-co ju-ar">
      <div class="pie" ref="chart"></div>
      <div class="sign">
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#ffa336;"></div>
          <p style="margin-left:-1.5rem;">
            国企:
            <span>300</span>家
          </p>
          <div style="background-color:#fe9ea0;"></div>
          <p style="margin-right:-0.03rem;">
           私企:
            <span>300</span>家
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#7eb9ff;"></div>
          <p>
            事业单位:
            <span>150</span>家
          </p>
          <div style="background-color:#67c259;"></div>
          <p>
            岗位数量:
            <span>150</span>个
          </p>
        </div>
        <div class="sign1 fl-ex al-it">
          <div style="background-color:#2ef2e8;"></div>
          <p style="margin-left:1rem;">
            求职人员:
            <span>2000</span>人
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    initCharts() {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        tooltip: {
        },
        calculable: true,
        series: [
          {
            color: [
              
              "#2ef2e8", //青色
              "#67c259", //绿色
               "#7eb9ff", //蓝色
              "#fe9ea0", //粉色   
              "#ffa336", //黄色        
            ],
            type: "pie",
            radius: [30, 125],
            center: ["50%", "55%"],
            roseType: "radius",
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: [
              { value: 14, name: "求职人员2000人" },
              { value: 10, name: "岗位数量150个" },
              { value: 10, name: "事业单位150家" },
              { value: 12, name: "私企300家" },
              { value: 12, name: "国企300家" }
            ]
          }
        ]
      });
    }
  },
  mounted() {
    this.initCharts();
  }
};
</script>

<style scoped>
.container1 {
  width: 100%;
  height: 27rem;
}
.container1 .pie {
  width: 90%;
  height: 19rem;
  margin: 0 auto;
}
.container1 .sign {
  width: 77%;
  height: 4.5rem;
  margin: 0 auto;
}
.container1 .sign .sign1 {
  width: 100%;
  height:1.5rem;
}
.container1 .sign .sign1 > div {
  width: 1.2rem;
  height: 0.5rem;
}
.container1 .sign .sign1 > p {
  font-size: 0.85rem;
  color: black;
  text-align: left;
}
</style>


